.main {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  &__feature {
    position: absolute;
    margin: 0;
    padding: 0;
    color: white;
    bottom: 30%;
    max-width: 309px;
    font-size: 16px;
  }
  &__callback {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    cursor: pointer;
    height: 48px;
    width: 168px;
    box-sizing: border-box;
    border-radius: 5px;
    border: none;
    color: white;
    background: #780810;
  }

  &__wrapper {
    margin-top: 280px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__social {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: 10%;
    left: 10%;
    gap: 30px;
    &:before {
      content: '';
      width: 1px;
      height: 300px;
      background-color: #fff;

    }
  }
  &__arrow {
    cursor: pointer;
    width: 64px;
    height: 64px;
    color: white;
    font-weight: 400;
    font-size: 32px;
    line-height: 100%;
    border: none;
    background: transparent;
    background-image: url(../Images/arrowPrew.svg);
    &Down {
      background-image: url(../Images/down.svg);
    }
    &Container {
      justify-content: center;
      align-items: center;
      gap: 20px;
      bottom: 50%;
      right: 7%;
      position: absolute;
      display: flex;
      flex-direction: column;
    }
  }
  &__container {
    position: relative;
    max-width: 984px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
  }
  &__buttons {
    display: flex;
    gap: 30px;
  }
  &__about {
    border: solid 1px white;
    background: transparent;
  }
  &__title {
    margin: 0 0 40px 0;
    text-align: left;
    line-height: 34px;
    max-width: 508px;
    font-size: 64px;
    color: #D9D9D9;

    &Span {
      font-family: Montserrat, sans-serif;
      font-size: 24px;
      line-height: 29px;
    }
  }

  &__text {
    margin-bottom: 18px;
    font-family: Furore, sans-serif;
    font-style: normal;
    color: #D9D9D9;
  }
}