.header {
  background: linear-gradient(180deg, #0D0C0C 0%, rgba(13, 12, 12, 0.830292) 72.92%, rgba(13, 12, 12, 0.625) 87.5%, rgba(13, 12, 12, 0) 100%);
  z-index: 1;
  color: white;
  height: 55px;
  position: fixed;
  width: 100%;

  &__container {
    align-content: center;
    align-items: center;
    margin: 0 auto;
    justify-content: space-between;
    display: flex;
    max-width: 984px;
  }

  &__phone {

    text-decoration: none;
    color: white;
    font-family: Montserrat-bold, sans-serif;
  }

  &__list {
    gap: 40px;
    display: flex;
    list-style: none;

    &Item {
      font-family: Montserrat-bold, sans-serif;
    }
  }
}