.container {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  background-color: #1E1E1E;
}
.BeforeAfterSlider {
  max-width: 380px;
}
.wrapper {
  max-height: 500px;
  justify-content: space-between;
  width: 100%;
  background: rgba(126, 126, 126, 0.2);
  backdrop-filter: blur(25px);
  /* Note: backdrop-filter has minimal browser support */
  border-radius: 20px;
  max-width: 1180px;
  display: flex;
  padding: 75px 130px;
  box-sizing: content-box;
}
.optgroup {
  background: #1C1B1B;
}
option{
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color:#fff;
  background: #1C1B1B;

}
.form {
  max-width: 500px;
  height: 100%;
  &__select {
    color: white;
    border-radius: 5px;
    outline: none;
    border: 2px solid white;
    background-color: transparent;
    height: 32px;

    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    width: 142px;
  }
  &__container {
    display: flex;
    flex-direction: column;
  }
  &__wrapper {
    display: flex;
  }
  &__label {
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    color: white;
  }
  &__title {
    font-family: 'Furore', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 41px;
    color: white;
  }
}